@charset "UTF-8";
/*基础样式*/
.table-wrap *,
.grid-menu * {
  box-sizing: border-box !important;
  color: #333;
  margin: 0;
  padding: 0; }

.table-wrap fieldset,
.table-wrap img {
  border-style: none;
  border-color: inherit;
  border-width: medium; }

.table-wrap ul,
.table-wrap li,
.grid-menu ul,
.grid-menu li {
  list-style-type: none;
  margin: 0px;
  padding: 0px; }

.table-wrap .clear {
  clear: both; }

/*禁用文字选中*/
.no-select-text {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none; }

/*table配置中样式*/
table[grid-manager] {
  visibility: hidden; }

table[grid-manager].GridManager-ready {
  visibility: visible; }

/*table所在的DIV*/
.table-wrap {
  position: relative;
  /*background-color: #f8f8f8;*/
  /*overflow: hidden;*/
  /*padding: 3px;*/ }

/*用户交互*/
.table-wrap[user-interactive="Adjust"] .table-div {
  overflow: hidden; }

.table-wrap[user-interactive="Drag"] .table-div {
  overflow: hidden; }

.table-div {
  overflow: auto;
  position: relative; }

.table-div.config-editing {
  /*用于配置列表功能*/
  overflow-x: hidden; }

/*table*/
.table-wrap table[grid-manager] {
  table-layout: fixed;
  word-wrap: break-word;
  text-align: center;
  background-color: #ddd;
  border-radius: 4px;
  width: 100%;
  border-collapse: separate;
  border-spacing: 1px; }
  .table-wrap table[grid-manager].table-fixed-left {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: auto; }
  .table-wrap table[grid-manager].table-fixed-right {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    width: auto; }

.table-wrap table[grid-manager] tr td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.table-wrap table[grid-manager] td[gm-disabled="true"] {
  color: #ccc; }

.table-wrap table[grid-manager] th[th-visible="none"],
.table-wrap table[grid-manager] td[td-visible="none"] {
  display: none; }

.table-wrap table[grid-manager] th[th-visible="visible"],
.table-wrap table[grid-manager] td[td-visible="visible"] {
  display: table-cell; }

.table-wrap table[grid-manager] th .th-text {
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.table-wrap table[grid-manager] thead tr th {
  background-color: #fafafa;
  font-weight: bold; }

.table-wrap table[grid-manager] tbody tr td {
  background-color: #fff;
  padding: 6px;
  vertical-align: middle; }

.table-wrap table[grid-manager] tbody tr:nth-child(2n) td {
  background-color: #fafafa; }

.table-wrap table[grid-manager] tbody tr[checked="checked"] td {
  background-color: #ffb; }

.table-wrap table[grid-manager] tbody tr[row-hover="true"] td {
  background-color: rgba(255, 255, 187, 0.8); }

/*列表设置显示隐藏*/
.table-wrap .config-area {
  display: none;
  max-width: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  cursor: pointer;
  z-index: 3; }

.table-wrap .config-area .config-action {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 5px;
  top: 9px;
  overflow: hidden;
  text-align: center;
  z-index: 2; }

.table-wrap .config-area .config-action i {
  display: inline-block;
  font-size: 18px; }

.table-wrap .config-area .config-action:hover i {
  color: #09f; }

.table-wrap .config-area .config-list {
  display: block;
  width: 100%;
  padding: 2px 30px 0px 2px;
  list-style-type: none;
  background: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 3px;
  margin: 0px; }

.table-wrap .config-area .config-list > li {
  display: inline-block;
  padding: 4px 10px;
  line-height: 20px;
  background-color: #e8e8e8;
  margin-right: 2px;
  margin-bottom: 2px; }

.table-wrap .config-area .config-list > li > label {
  margin: 0px; }

.table-wrap .config-area .config-list li.no-click {
  cursor: not-allowed; }

.table-wrap .config-area .config-list li.no-click * {
  cursor: not-allowed; }

.table-wrap .config-area .config-list li input[type="checkbox"] {
  display: none; }

.table-wrap .config-area .config-list li label {
  cursor: pointer;
  color: #666;
  font-weight: 500; }

.table-wrap .config-area .config-list li:hover label {
  color: #09f; }

.table-wrap .config-area .config-list li.no-click:hover label {
  color: #666; }

.table-wrap .config-area .config-list li .fake-checkbox {
