  width: 100%;
  border-collapse: separate;
  border-spacing: 1px;
  font-family: arial !important; }

.table-wrap .dreamland-div .dreamland-table thead tr th {
  background-color: rgba(0, 165, 60, 0.1);
  text-align: center; }

.table-wrap .dreamland-div .dreamland-table thead tr th:hover {
