.table-wrap table[grid-manager] th[th-name="action"] {
  cursor: default; }

.table-wrap table[grid-manager] [gm-checkbox="true"] input[type=checkbox] {
  vertical-align: middle; }

.table-wrap table[grid-manager] td .plugin-action {
  color: #5a82be;
  margin: auto 5px;
