/*文本对齐*/
.table-wrap th {
  text-align: center; }

.table-wrap td {
  text-align: left; }

.table-wrap[gm-text-align="left"] th,
.table-wrap[gm-text-align="left"] td {
  text-align: left; }

.table-wrap[gm-text-align="right"] th,
.table-wrap[gm-text-align="right"] td {
  text-align: right; }

.table-wrap[gm-text-align="center"] th,
.table-wrap[gm-text-align="center"] td {
  text-align: center; }

.table-wrap th[align="left"],
.table-wrap td[align="left"] {
  text-align: left; }

.table-wrap th[align="right"],
.table-wrap td[align="right"] {
  text-align: right; }

.table-wrap th[align="center"],
.table-wrap td[align="center"] {
