/*空模板样式*/
.table-wrap table[grid-manager] tr[emptytemplate] td {
  padding: 0; }

.table-wrap table[grid-manager] .gm-emptyTemplate {
  width: 100%;
  height: 100px;
  line-height: 100px;
  text-align: center;
  font-size: 24px;
  color: #ddd;
  background-color: #f8f8f8; }

/*遮罩*/
.table-wrap .mask-element {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #000;
  opacity: 0.6;
  z-index: 9; }

/*闪烁动画特效*/
@-webkit-keyframes opacityChange {
  0% {
    opacity: 0.1; }
  100% {
    opacity: 0.7; } }

@-moz-keyframes opacityChange {
  0% {
    opacity: 0.1; }
  100% {
    opacity: 0.7; } }

@keyframes opacityChange {
  0% {
    opacity: 0.1; }
  100% {
    opacity: 0.7; } }

/*旋转动画特效*/
@-webkit-keyframes rotationMedia {
  0% {
    -webkit-transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg); } }

@-moz-keyframes rotationMedia {
  0% {
    -moz-transform: rotate(0deg); }
  100% {
    -moz-transform: rotate(360deg); } }

@keyframes rotationMedia {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.hidden {
  display: none; }

