/*loading*/
.table-wrap .loading {
  z-index: 999;
  position: absolute;
  left: 0px;
  top: 100px;
  width: 100%;
  height: 100%;
  background: #fff;
  opacity: 0.7; }

.table-wrap .loading .kernel {
  width: 30px;
  height: 30px;
  border: 2px #00a53c solid;
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  -webkit-animation: gm_fill_color 5s linear infinite;
  -moz-animation: gm_fill_color 5s linear infinite;
  animation: gm_fill_color 5s linear infinite; }

.table-wrap .loading .kernel:after {
  width: 4px;
  height: 4px;
  position: absolute;
  content: "";
  background-color: #4078c0;
  top: -8px;
  left: 0px;
  -webkit-animation: gm_square_check 1s ease-in-out infinite;
  -moz-animation: gm_square_check 1s ease-in-out infinite;
  animation: gm_square_check 1s ease-in-out infinite; }

@-webkit-keyframes gm_square_check {
  25% {
    left: 32px;
    top: -8px; }
  50% {
    left: 32px;
    top: 32px; }
  75% {
    left: -9px;
    top: 32px; }
  100% {
    left: -9px;
    top: -7px; } }

@-moz-keyframes gm_square_check {
  25% {
    left: 32px;
    top: -8px; }
  50% {
    left: 32px;
    top: 32px; }
  75% {
    left: -9px;
    top: 32px; }
  100% {
    left: -9px;
    top: -7px; } }

@keyframes gm_square_check {
  25% {
    left: 32px;
    top: -8px; }
  50% {
    left: 32px;
    top: 32px; }
  75% {
    left: -9px;
    top: 32px; }
  100% {
    left: -9px;
    top: -7px; } }

@-webkit-keyframes gm_fill_color {
  0% {
    box-shadow: inset 0px 0px 0px 0px rgba(0, 165, 60, 0.1); }
  100% {
    box-shadow: inset 0px -20px 0px 0px #00a53c; } }

@-moz-keyframes gm_fill_color {
  0% {
    box-shadow: inset 0px 0px 0px 0px rgba(0, 165, 60, 0.1); }
  100% {
    box-shadow: inset 0px -20px 0px 0px #00a53c; } }

@keyframes gm_fill_color {
  0% {
    box-shadow: inset 0px 0px 0px 0px rgba(0, 165, 60, 0.1); }
  100% {
    box-shadow: inset 0px -20px 0px 0px #00a53c; } }
