/*基础样式*/

.table-wrap *,
.grid-menu * {
  box-sizing: border-box !important;
  color: #333;
  margin: 0;
  padding: 0;
}

.table-wrap fieldset,
.table-wrap img {
  border-style: none;
  border-color: inherit;
  border-width: medium;
}

.table-wrap ul,
.table-wrap li,
.grid-menu ul,
.grid-menu li {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

.table-wrap .clear {
  clear: both;
}


/*禁用文字选中*/

.no-select-text {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}


/*table配置中样式*/

table[grid-manager] {
  visibility: hidden;
}

table[grid-manager].GridManager-ready {
  visibility: visible;
}


/*table所在的DIV*/

.table-wrap {
  position: relative;
  /*background-color: #f8f8f8;*/
  /*overflow: hidden;*/
  /*padding: 3px;*/
}


/*用户交互*/

.table-wrap[user-interactive="Adjust"] .table-div {
  overflow: hidden;
}

.table-wrap[user-interactive="Drag"] .table-div {
  overflow: hidden;
}

.table-div {
  overflow: auto;
  position: relative;
}

