/*table*/

.table-wrap table[grid-manager] {
  table-layout: fixed;
  word-wrap: break-word;
  text-align: center;
  background-color: #ddd;
  border-radius: 4px;
  width: 100%;
  border-collapse: separate;
  border-spacing: 1px;
  &.table-fixed-left {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: auto;
  }
  &.table-fixed-right {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    width: auto;
  }
}

.table-wrap table[grid-manager] tr td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.table-wrap table[grid-manager] td[gm-disabled="true"] {
  color: #ccc;
}

.table-wrap table[grid-manager] th[th-visible="none"],
.table-wrap table[grid-manager] td[td-visible="none"] {
  display: none;
}

.table-wrap table[grid-manager] th[th-visible="visible"],
.table-wrap table[grid-manager] td[td-visible="visible"] {
  display: table-cell;
}

.table-wrap table[grid-manager] th .th-text {
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.table-wrap table[grid-manager] thead tr th {
  background-color: #fafafa;
  font-weight: bold;
}

// .table-wrap table[grid-manager] thead tr th:hover {
//   background-color: #8dbdd8;
// }
.table-wrap table[grid-manager] tbody tr td {
  background-color: #fff;
  padding: 6px;
  vertical-align: middle;
}

.table-wrap table[grid-manager] tbody tr:nth-child(2n) td {
  background-color: #fafafa;
}

.table-wrap table[grid-manager] tbody tr[checked="checked"] td {
  background-color: #ffb;
}

// .table-wrap table[grid-manager] tbody tr td[col-hover="true"] {
//   background-color: #e6eeee;
// }
.table-wrap table[grid-manager] tbody tr[row-hover="true"] td {
  background-color: rgba(#ffb, .8);
}


/*列表设置显示隐藏*/

.table-wrap .config-area {
  display: none;
  max-width: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  cursor: pointer;
  z-index: 3;
}

.table-wrap .config-area .config-action {
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 5px;
  top: 9px;
  overflow: hidden;
  text-align: center;
  z-index: 2;
}

.table-wrap .config-area .config-action i {
  display: inline-block;
  font-size: 18px;
}

.table-wrap .config-area .config-action:hover i {
  color: #09f;
}

.table-wrap .config-area .config-list {
  display: block;
  width: 100%;
  padding: 2px 30px 0px 2px;
  list-style-type: none;
  background: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 3px;
  margin: 0px;
}

.table-wrap .config-area .config-list>li {
  display: inline-block;
  padding: 4px 10px;
  line-height: 20px;
  background-color: #e8e8e8;
  margin-right: 2px;
  margin-bottom: 2px;
}

.table-wrap .config-area .config-list>li>label {
  margin: 0px;
}

.table-wrap .config-area .config-list li.no-click {
  cursor: not-allowed;
}

.table-wrap .config-area .config-list li.no-click * {
  cursor: not-allowed;
}

.table-wrap .config-area .config-list li input[type="checkbox"] {
  display: none;
}

.table-wrap .config-area .config-list li label {
  cursor: pointer;
  color: #666;
  font-weight: 500;
}

.table-wrap .config-area .config-list li:hover label {
  color: #09f;
}

.table-wrap .config-area .config-list li.no-click:hover label {
  color: #666;
}

.table-wrap .config-area .config-list li .fake-checkbox {
  display: inline-block;
  width: 14px;
  height: 14px;
  position: relative;
  border: 1px solid #999;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px #e8e8e8;
  vertical-align: middle;
  margin-right: 5px;
}

.table-wrap .config-area .config-list li .fake-checkbox:after {
  display: block;
  width: 8px;
  height: 8px;
  position: absolute;
  top: 2px;
  left: 2px;
  content: "";
  background: #e8e8e8;
  border-radius: 50%;
}

.table-wrap .config-area .config-list li:hover .fake-checkbox {
  border-color: #09f;
}

.table-wrap .config-area .config-list li.checked-li .fake-checkbox:after {
  background: #09f;
}


/*表头提醒*/

.table-wrap table[grid-manager] th .remind-action {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 6px;
  left: 4px;
  cursor: help;
}

.table-wrap table[grid-manager] th .remind-action .ra-area {
  display: none;
  min-width: 150px;
  max-width: 300px;
  position: absolute;
  top: 80%;
  left: 80%;
  padding: 12px;
  border: 3px solid #000;
  z-index: 9999;
  border-radius: 5px;
  box-shadow: 3px 3px 10px 0px #222;
  background-color: #000;
  opacity: 0.8;
  line-height: 14px;
  color: #fff;
}

.table-wrap table[grid-manager] .set-top th .remind-action .ra-area {
  visibility: hidden;
  display: none;
}

.table-wrap table[grid-manager] .set-top th .remind-action:hover .ra-area {
  visibility: visible;
  display: block !important;
}

.table-wrap table[grid-manager] th .remind-action .ra-help {
  display: block;
  width: 16px;
  height: 16px;
  color: #666;
}

.table-wrap table[grid-manager] th .remind-action:hover .ra-help {
  color: #2b669a;
}

.table-wrap table[grid-manager] th .remind-action .ra-area .ra-title {
  display: block;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 1.1em;
  color: #FF6;
}

.table-wrap table[grid-manager] th .remind-action .ra-area .ra-con {
  display: block;
  font-weight: 500;
  text-align: left;
  font-size: 0.9em;
  color: #fff;
}


/*导出excel*/

#gm-export-action {
  display: none;
}


/*表头置顶*/

.table-wrap .set-top {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  background-color: #ddd;
}

.table-wrap .set-top .adjust-action {
  /*display:none !important;*/
}

.table-wrap .set-top .sorting-action {
  /*display:none;*/
}

.table-wrap .set-top .drag-action {
  /*cursor:default !important;*/
}

.table-wrap table[grid-manager] thead.scrolling {
  visibility: hidden;
}


/*th内的上级DIV*/

.table-wrap table[grid-manager] th .th-wrap {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 6px;
}

// .table-wrap table[grid-manager] th[remind] .th-wrap {
//   padding-left: 24px;
// }
// .table-wrap table[grid-manager] th[sorting] .th-wrap {
//   padding-right: 24px;
// }
.table-wrap table[grid-manager] th[gm-create="true"] .th-wrap {
  padding: 6px;
}


/*宽度调整*/

.table-wrap table[grid-manager] th .adjust-action {
  display: block;
  width: 6px;
  height: 100%;
  position: absolute;
  top: 0;
  right: -4px;
  cursor: col-resize;
  z-index: 2;
}

.table-wrap table[grid-manager] .adjust-selected {
  border-right-width: 2px;
  border-right-style: dashed;
  border-right-color: #ccc;
}

.table-wrap table[grid-manager] .adjust-selected .drag-action {
  cursor: col-resize;
}


/*文本镜像：用于实时获取th的文本宽度*/

.table-wrap .text-dreamland {
  position: absolute;
  top: 0px;
  left: 0px;
  white-space: nowrap;
  visibility: hidden;
  z-index: -1;
}


/*排序*/

.table-wrap table[grid-manager] .sorting-action {
  display: block;
  width: 20px;
  height: 18px;
  position: absolute;
  top: 6px;
  right: 5px;
  cursor: default;
  z-index: 2;
  color: #444;
}

.table-wrap table[grid-manager] .sorting-action:hover {
  color: #000;
}

.table-wrap table[grid-manager] .sorting-action.sortion-up {
  content: "\f102";
}

.table-wrap table[grid-manager] .sorting-action.sortion-down {
  content: "\f103";
}

.table-wrap table[grid-manager] .sorting-action .sa-icon {
  display: block;
  height: 10px;
  position: absolute;
}

.table-wrap table[grid-manager] .sorting-action .sa-up {
  top: 0px;
  right: 0px;
}

.table-wrap table[grid-manager] .sorting-action .sa-down {
  bottom: 0px;
  right: 0px;
}

.table-wrap table[grid-manager] .sorting-action.sorting-up .sa-up {
  opacity: 1;
}

.table-wrap table[grid-manager] .sorting-action.sorting-up .sa-down {
  opacity: 0.1;
}

.table-wrap table[grid-manager] .sorting-action.sorting-down .sa-up {
  opacity: 0.1;
}

.table-wrap table[grid-manager] .sorting-action.sorting-down .sa-down {
  opacity: 1;
}


/*拖拽换位*/

.table-wrap table[grid-manager] th .drag-action {
  cursor: all-scroll;
}

.table-wrap table[grid-manager] .drag-ongoing {
  cursor: all-scroll;
  background: #666 !important;
  border-color: #ccc !important;
  color: #fff !important;
  opacity: 0.5;
}

.table-wrap table[grid-manager] .drag-ongoing.opacityChange {
  opacity: 1;
  -webkit-animation: opacityChange 1s ease-in-out infinite;
  -moz-animation: opacityChange 1s ease-in-out infinite;
  animation: opacityChange 1s ease-in-out infinite;
}

.table-wrap .dreamland-div {
  display: none;
  position: absolute;
  border: 1px solid #ccc;
  padding: 0;
  background: #fff;
  cursor: all-scroll;
  z-index: 9;
}

.table-wrap .dreamland-div .dreamland-table {
  table-layout: fixed;
  width: 100%;
  margin: 0 !important;
  padding: 0 !important;
}

.table-wrap .dreamland-div .dreamland-table {
  background-color: #dad9c7;
  width: 100%;
  border-collapse: separate;
  border-spacing: 1px;
  font-family: arial !important;
}

.table-wrap .dreamland-div .dreamland-table thead tr th {
  background-color: rgba(#00a53c, .1);
  text-align: center;
}

.table-wrap .dreamland-div .dreamland-table thead tr th:hover {
  background-color: rgba(#00a53c, .5);
}

.table-wrap .dreamland-div .dreamland-table tbody tr td {
  background-color: #fff;
  color: #3d3d3d;
  padding: 4px;
  vertical-align: top;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


/*由插件生成的序号、全选样式*/

.table-wrap table[grid-manager] [gm-create="true"] {
  text-align: center;
  cursor: default;
}

.table-wrap table[grid-manager] th[th-name="gm_checkbox"],
.table-wrap table[grid-manager] th[th-name="action"] {
  background-color: rgba(#fafafa, .9);
}

.table-wrap table[grid-manager] th[th-name="action"] {
  cursor: default;
}

.table-wrap table[grid-manager] [gm-checkbox="true"] input[type=checkbox] {
  vertical-align: middle;
}

.table-wrap table[grid-manager] td .plugin-action {
  color: #5a82be;
  margin: auto 5px;
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}


/*文本对齐*/

.table-wrap th {
  text-align: center;
}

.table-wrap td {
  text-align: left;
}

.table-wrap[gm-text-align="left"] th,
.table-wrap[gm-text-align="left"] td {
  text-align: left;
}

.table-wrap[gm-text-align="right"] th,
.table-wrap[gm-text-align="right"] td {
  text-align: right;
}

.table-wrap[gm-text-align="center"] th,
.table-wrap[gm-text-align="center"] td {
  text-align: center;
}

.table-wrap th[align="left"],
.table-wrap td[align="left"] {
  text-align: left;
}

.table-wrap th[align="right"],
.table-wrap td[align="right"] {
  text-align: right;
}

.table-wrap th[align="center"],
.table-wrap td[align="center"] {
  text-align: center;
}


/*空模板样式*/

.table-wrap table[grid-manager] tr[emptytemplate] td {
  padding: 0;
}

.table-wrap table[grid-manager] .gm-emptyTemplate {
  width: 100%;
  height: 100px;
  line-height: 100px;
  text-align: center;
  font-size: 24px;
  color: #ddd;
  background-color: #f8f8f8;
}


/*遮罩*/

.table-wrap .mask-element {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #000;
  opacity: 0.6;
  z-index: 9;
}


/*闪烁动画特效*/

@-webkit-keyframes opacityChange {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 0.7;
  }
}

@-moz-keyframes opacityChange {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 0.7;
  }
}

@keyframes opacityChange {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 0.7;
  }
}


/*旋转动画特效*/

@-webkit-keyframes rotationMedia {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@-moz-keyframes rotationMedia {
  0% {
    -moz-transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
  }
}

@keyframes rotationMedia {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}


